<template>
  <div class="QuestionsList" id="question_list">
    <div data-spy="scroll" data-target="#question_list" data-offset="0" id="list">
    <table v-for="question in questions" class="table table-hover" >
      <tbody>
        <tr> 
          <td data-toggle="modal"
data-target="#myModal">
            <p class="pull-left">{{ question.question_desc }}</p>
          </td>
          <!-- <td><button class="btn btn-success" data-toggle="modal"
data-target="#myModal">X</button></td> -->
        </tr>
      </tbody>
    </table>
 
  <!-- Modal -->
  <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close"
          data-dismiss="modal" aria-hidden="true">
          &times;
          </button>
          <h4 class="modal-title" id="myModalLabel">
            This Modal title
          </h4>
          </div>
            <div class="modal-body">
              Add some text here
            </div>
            <div class="modal-footer">
              <div class="row ">

                <button type="button" name="edit_question" class="btn btn-primary pull-left col-md-5 col-md-offset-1">Edit</button>
                <button type="button" class="btn btn-danger col-md-5" name="delete_question">
                Delete
              </button>

              </div>
              
            </div>
         </div>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
  /* eslint-disable */
  import {AXIOS} from './http-common'

export default {
  name: 'QuestionsList',
  errors: '',
  props: ['questions'],

  data () {
    return {
      // questions: ['Question 1', 'Question 2', 'Question 3', 'Question 4', 'Question 5', 'Question 6']
    }
  },
  methods: {
      testRestService() {
          AXIOS.get("api/test")
            .then(response => {
                this.msg = response.data;
                console.log("Success " + response.data);
            })
            .catch(e => {
                console.log("Failure " + e);
            })
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
li {
  display: inline-block;
  margin: 0;
}
a {
  color: #42b983;
}
#list {
  max-height:600px;
  overflow:auto; 
  position: relative;
}
</style>



